import { UtilService } from './../../servicios/util.service';
import { Ticket } from './../../modelo/ticket';
import { TicketService } from './../../servicios/ticket.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-admin-historico',
  templateUrl: './admin-historico.component.html',
  styleUrls: ['./admin-historico.component.css'],
})
export class AdminHistoricoComponent implements OnInit {

  constructor(
    private ticketService: TicketService,
    private utilService: UtilService
  ) {}

  columns = [
    'Numero de Ticket',
    'Matricula',
    'Hora de entrada',
    'Hora de salida',
  ];
  index = ['id', 'matricula', 'entrada', 'salida'];
  tickets: Ticket[] = [];

  ngOnInit(): void {
    this.ticketService.getTickets().subscribe(
      (response) => {
        response.forEach((value, index) => {
          value.entrada = this.utilService.formatearFecha(
            new Date(value.entrada)
          );
          if (value.salida != null) {
            value.salida = this.utilService.formatearFecha(
              new Date(value.salida)
            );
          } else {
            value.salida = 'En parking';
          }
        });
        this.tickets = response;
      },
      (error) => console.log(error)
    );
  }
}
